body {
  margin: 15px;
}

.watch {
  margin-top: 15px auto;
  border-radius: 50%;
  width: 600px;
  height: 600px;
  position: relative;
}

.watch__seconds {
  background-color: #8a2be2;
  border-radius: 2px;
  width: 4px;
  height: 300px;
  animation: 60s steps(60, end) infinite sec-rotate;
  position: absolute;
  top: 22px;
  left: 298px;
}

.watch__seconds:target {
  animation-name: sec-rotate;
  animation-play-state: paused;
}

.watch__minutes {
  background-color: #adff2f;
  border-radius: 3px;
  width: 6px;
  height: 220px;
  animation: 3600s steps(60, end) infinite min-rotate;
  position: absolute;
  top: 102px;
  left: 298px;
}

.watch__minutes:target {
  animation-play-state: paused;
}

.watch__stop {
  z-index: 1;
  background-color: tomato;
  border-radius: 15px;
  width: 30px;
  height: 30px;
  position: absolute;
  top: 285px;
  left: 285px;
}

.watch__stop:hover {
  cursor: pointer;
  animation-name: sec-rotate;
  animation-play-state: paused;
}

